@import url(https://fonts.googleapis.com/css2?family=Poppins:ital@1&display=swap" rel="stylesheet);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    text-align: center;
}

* {
    box-sizing: border-box;
}

/*body {
    overflow-y: auto;
}*/

.background {
    min-height: 100vh;
    min-width: 100vw;
    max-height:-webkit-fit-content;
    max-height:-moz-fit-content;
    max-height:fit-content;
    max-width:100%;
    overflow-y: auto;
    overflow-x: auto;
}

.header {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    flex-wrap: wrap;
    padding-top: 7vh;
}

.header > div > div > a > img {
    -webkit-transform: scale(1.2) rotate(45deg);
            transform: scale(1.2) rotate(45deg);
}

.header > div > div > h1 {
    font-size:40px;
    font-family: 'Courier New', Courier, monospace;
    padding-top: 3vh;
}

.header-column {
    width: 33.33%;
    float: left;
}

.footer {
    margin-top:20vh;
}

.footer-list {
    padding: 0px;
}

.footer-column {
    width: 25%;
    float: left;
}

.social-links-column {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 0px;
}

.buttons {
    cursor: pointer;
}

.team {
    display: flex;
    max-height:-webkit-fit-content;
    max-height:-moz-fit-content;
    max-height:fit-content;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    max-width: 100vw;
    text-align: center;
    justify-content: space-around;
    position: relative;
    flex-wrap: wrap;
    width: 33.33%;
}

.team-images {
    text-align: center;
}

.team-column {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.team-column > div {
    margin-top: -10vh;
}

#connectButton {
    font-size: 20px;
    font-family:'Courier New', Courier, monospace;
    color:beige;
    background-color:transparent;
    border-width: 0px;
    cursor: pointer;
    padding-top: 3vh;
    background: none;
}

#connect-btn2 {
    font-size: 14px;
    font-family:'Courier New', Courier, monospace;
    color: black;
    border-width: 1px;
    cursor: pointer;
}

#connect-btn2:hover {
    color:darkkhaki;
}

#switchButton {
    font-size: 20px;
    font-family:'Courier New', Courier, monospace;
    color:beige;
    border-width: 0px;
    background: none;
    padding-top: 3vh;
    cursor: pointer;
    background-color: transparent;
}

#user-address {
    float: right;
    color: beige;
    font-style: italic;
    border: none;
    background: blueviolet;
    display: flex;
    cursor: pointer;
}

#just-address {
    background: greenyellow;
}

#indi * {
    border-radius: 20px;
    background-color: transparent;
}

#welcome {
    color: rgb(212, 196, 221);
    font-size: 28px;
    font-style: italic;
    font-family:'Courier New', Courier, monospace;
}

#detail {
    color: rgb(209, 94, 148);
    font-size: 22px;
    font-family:'Courier New', Courier, monospace;
}

#header-name {
    color: beige;
    margin: 0px;
}

.body#website_gif {
    size: 10vh;
}

#mint_title {
    text-align: left;
    color: beige;
    size: 28px;
    padding-left: 16vw;
    margin-top: 20vh;
}

#mint_box {
    border: 2px solid rgb(133, 161, 163);
    text-align: center;
    list-style: none;
    width: 100%;
    align-items: center;
    overflow-x: auto;
}

.mint {
    display: flex;
    width:100%;
}

.alert {
    padding: 10vw;
    background-color: rgb(192, 185, 185);
    border-radius: 12px;
    color: black;
    margin-top: 20vh;
    position: relative;
}

.close-btn {
    cursor: pointer;
    font-weight: bold;
    font-size: 28px;
    position: absolute;
    top: 0%;
    right:3%;
}

.close-btn:hover {
    color: red;
}

#copyright {
    color:beige;
}

#mint_button_text {
    font-size: 22px;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    background-color:rgb(207, 173, 173);
    cursor: pointer;
}

#mint_box_items {
    display: flex;
}


#creator-text {
    float: left;
    color: rgb(207, 207, 123);
}

#creator-image {
    -webkit-transform: scale(0.4);
            transform: scale(0.4);
}

#codeveloper-text {
    float: left;
    color:rgb(207, 207, 123);
    padding-left: 0vw;
}

#codeveloper-image {
    -webkit-transform: scale(0.42);
            transform: scale(0.42);
}

#artist-text {
    float: left;
    color: rgb(207, 207, 123);
    left: 10vw;
}

#artist-image {
    -webkit-transform: scale(0.4);
            transform: scale(0.4);
}

